const DealCard = ({ deal }) => {
  const {
    title,
    image_url,
    original_price,
    sales_price,
    rating,
    product_url,
    promo_code,
    seller,
  } = deal;

  const discount_percentage = Math.round(
    ((original_price - sales_price) / original_price) * 100
  );

  let USDollar = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
  });

  const getDiscountClass = (percentage) => {
    if (percentage >= 50) return "discount-high";
    if (percentage >= 30) return "discount-medium";
    return "discount-low";
  };

  const getPriceClass = (percentage) => {
    if (percentage >= 50) return "sales-price-high";
    if (percentage >= 30) return "sales-price-medium";
    return "sales-price-low";
  };

  const star = <span className="star">‚òÖ</span>;

  return (
    <div className="deal-card">
      {promo_code ? (
        <div className="hh-promo-code-wrapper">
          <p>Use Code {promo_code}</p>
        </div>
      ) : (
        <></>
      )}
      <a
        href={product_url}
        className="deal-card-link"
        target="_blank"
        rel="noopener noreferrer"
      >
        <div className="deal-image">
          {image_url ? (
            <img src={image_url} alt={title} />
          ) : (
            <div className="no-image">No Image Available</div>
          )}
        </div>
        <div className="deal-content">
          <h3 className="deal-title">{title}</h3>
          <div className="deal-pricing">
            <span
              className={`sales-price ${getPriceClass(discount_percentage)}`}
            >
              {USDollar.format(sales_price)}
            </span>
            {original_price && (
              <span className="original-price">
                {USDollar.format(original_price)}
              </span>
            )}
            {discount_percentage > 0 && (
              <span
                className={`discount ${getDiscountClass(discount_percentage)}`}
              >
                {discount_percentage}% OFF
              </span>
            )}
          </div>
          <div className="deal-rating">
            <div className="rating-stars">
              {Array.from({ length: 5 }, (_, i) => (
                <span key={i} className={`star ${i < rating ? "filled" : ""}`}>
                  ‚òÖ
                </span>
              ))}
            </div>
            <span className="rating-value">{rating}.0</span>
          </div>
          <div className="deal-seller">{seller}</div>
        </div>
      </a>
    </div>
  );
};

export default DealCard;
